.movieRow{
    margin-bottom: 30px;
}

.movieRow h2{
    margin: 0 0 0 30px;
}

.movieRow--listarea{
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list{
    width: 99999px;
}

.movieRow--item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img{
    width: 100%;
    transform: scale(0.9);
    transition: all 200ms ease-in;
}

.movieRow--item img:hover{
    transform: scale(1);
}